.AnswerItem {
    border: 1px solid white;
    border-radius: 5px;
    padding: 5px 10px;

    margin-bottom: 10px;
    cursor: pointer;
}

.AnswerItem:hover {
    background: rgba(255, 255, 255, 0.2);
    transition: background ease-in-out 0.3s;
}

.AnswerItem.success {
    background: rgba(161, 240, 69, 0.7);
}

.AnswerItem.error {
    background: rgba(240, 87, 108, 0.7);
}