.FinishedQuiz {
    padding: 20px;
    color: white;
    border: 1px solid white;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
}

.FinishedQuiz ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.FinishedQuiz ul li i {
    margin-left: 10px;
}

.success {
    color: rgba(161, 240, 69, 0.7);
}

.error {
    color: rgba(240, 87, 108, 0.7);
}


.FinishedQuiz button {
    display: block;
    margin: 10px auto;
}